/* Modal Overlay */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  padding: 20px;
}

.modal-container {
  background: white;
  border-radius: 12px;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  max-width: 800px;
  width: 100%;
  max-height: 90vh;
  overflow-y: auto;
  position: relative;
}

/* Modal Header */
.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px;
  background-color: #D4AF5F;
  border-bottom: 1px solid #e5e7eb;
  margin-bottom: 24px;
}

.modal-title {
  font-size: 1.5rem;
  font-weight: 600;
  color: #1f2937;
  margin: 0;
}

.close-btn {
  background: none;
  border: none;
  cursor: pointer;
  padding: 8px;
  border-radius: 6px;
  color: #6b7280;
  transition: all 0.2s;
}

.close-btn:hover {
  background-color: #f3f4f6;
  color: #374151;
}

/* Modal Body */
.modal-body {
  padding: 0 24px 24px 24px;
}

/* Partner Info Section */
.partner-info-section {
  margin-bottom: 32px;
}

.info-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 24px;
  flex-wrap: wrap;
  gap: 16px;
}

.partner-title-section {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 12px;
}

.partner-name {
  font-size: 1.75rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0;
}

.partner-badges {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
  align-items: center;
}

.category-badge {
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 0.875rem;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.category-0 { background-color: #dbeafe; color: #1e40af; }
.category-1 { background-color: #dcfce7; color: #166534; }
.category-2 { background-color: #fef3c7; color: #92400e; }
.category-3 { background-color: #fce7f3; color: #be185d; }
.category-4 { background-color: #e0e7ff; color: #3730a3; }
.category-5 { background-color: #f3f4f6; color: #374151; }

.status-badge {
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 0.875rem;
  font-weight: 500;
}

.status-active {
  background-color: #dcfce7;
  color: #166534;
}

.status-inactive {
  background-color: #fee2e2;
  color: #dc2626;
}

.info-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 16px;
}

.info-item {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.info-item label {
  font-weight: 600;
  color: #374151;
  font-size: 0.875rem;
}

.info-item span {
  color: #6b7280;
  font-size: 0.875rem;
}

.website-link a {
  color: #3b82f6;
  text-decoration: none;
}

.website-link a:hover {
  text-decoration: underline;
}

.note-text {
  background-color: #f9fafb;
  padding: 12px;
  border-radius: 6px;
  border-left: 3px solid #e5e7eb;
  font-style: italic;
}

/* Contacts Section */
.contacts-section {
  border-top: 1px solid #e5e7eb;
  padding-top: 24px;
}

.contacts-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.contacts-header h3 {
  font-size: 1.25rem;
  font-weight: 600;
  color: #1f2937;
  margin: 0;
}

/* Add Contact Form */
.add-contact-form {
  background-color: #f9fafb;
  padding: 20px;
  border-radius: 8px;
  margin-bottom: 24px;
  border: 1px solid #e5e7eb;
}

.add-contact-form h4 {
  margin: 0 0 16px 0;
  font-size: 1.125rem;
  font-weight: 600;
  color: #1f2937;
}

.form-row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 16px;
  margin-bottom: 16px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.form-group label {
  font-weight: 500;
  color: #374151;
  font-size: 0.875rem;
}

.form-control {
  padding: 8px 12px;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  font-size: 0.875rem;
  transition: border-color 0.2s;
}

.form-control:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.form-actions {
  display: flex;
  gap: 12px;
}

/* Contacts List */
.contacts-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.contact-item {
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  padding: 16px;
  background-color: white;
}

.contact-display {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 16px;
}

.contact-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.contact-field {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #374151;
  font-size: 0.875rem;
}

.contact-field svg {
  color: #6b7280;
  flex-shrink: 0;
}

.contact-actions {
  display: flex;
  gap: 8px;
  flex-shrink: 0;
}

.contact-edit {
  background-color: #f9fafb;
  padding: 16px;
  border-radius: 6px;
  border: 1px solid #e5e7eb;
}

.contact-edit h5 {
  margin: 0 0 16px 0;
  font-size: 1rem;
  font-weight: 600;
  color: #1f2937;
}

/* No Contacts */
.no-contacts {
  text-align: center;
  padding: 40px 20px;
  color: #6b7280;
}

.no-contacts svg {
  color: #d1d5db;
  margin-bottom: 16px;
}

.no-contacts p {
  margin: 0 0 20px 0;
  font-size: 1rem;
}

/* Buttons */
.btn {
  padding: 8px 16px;
  border-radius: 6px;
  font-size: 0.875rem;
  font-weight: 500;
  border: none;
  cursor: pointer;
  transition: all 0.2s;
  display: inline-flex;
  align-items: center;
  gap: 6px;
}

.btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.btn-primary {
  background-color: #B68A35;
  color: white;
}

.btn-primary:hover:not(:disabled) {
  background-color: #A87D2A;
}

.btn-secondary {
  background-color: #1B1D21;
  color: white;
}

.btn-secondary:hover:not(:disabled) {
  background-color: #2A2D33;
}

.btn-outline-primary {
  background-color: transparent;
  color: #B68A35;
  border: 1px solid #B68A35;
}

.btn-outline-primary:hover:not(:disabled) {
  background-color: #B68A35;
  color: white;
}

.btn-outline-danger {
  background-color: transparent;
  color: #C62828;
  border: 1px solid #C62828;
}

.btn-outline-danger:hover:not(:disabled) {
  background-color: #C62828;
  color: white;
}

.btn-sm {
  padding: 6px 12px;
  font-size: 0.75rem;
}

/* Loading Overlay */
.loading-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
}

.spinner {
  width: 32px;
  height: 32px;
  border: 3px solid #e5e7eb;
  border-top: 3px solid #3b82f6;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* RTL Support */
[dir="rtl"] .info-header {
  flex-direction: row-reverse;
}

[dir="rtl"] .contact-display {
  flex-direction: row-reverse;
}

[dir="rtl"] .form-actions {
  flex-direction: row-reverse;
}

[dir="rtl"] .contact-actions {
  flex-direction: row-reverse;
  order: -1;
}

[dir="rtl"] .contact-display {
  flex-direction: row-reverse;
}

[dir="rtl"] .contact-info {
  order: 2;
}

/* RTL Layout for Partner Header */
.info-header.rtl-layout {
  justify-content: flex-end;
  text-align: right;
}

.info-header.rtl-layout .partner-title-section {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  text-align: right;
}

.info-header.rtl-layout .partner-badges {
  justify-content: flex-end;
}

[dir="rtl"] .info-header {
  justify-content: flex-end;
  text-align: right;
}

[dir="rtl"] .partner-title-section {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  text-align: right;
}

[dir="rtl"] .partner-badges {
  justify-content: flex-end;
}

/* Responsive Design */
@media (max-width: 768px) {
  .modal-container {
    margin: 10px;
    max-height: calc(100vh - 20px);
  }
  
  .modal-header {
    padding: 16px 16px 0 16px;
  }
  
  .modal-body {
    padding: 0 16px 16px 16px;
  }
  
  .info-header {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .info-grid {
    grid-template-columns: 1fr;
  }
  
  .form-row {
    grid-template-columns: 1fr;
  }
  
  .contact-display {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .contact-actions {
    width: 100%;
    justify-content: flex-end;
  }
}
